.main-body {
    width: 100%;
    height: 100%;
    background-color: #fff;
    overflow: hidden;
    font-size: 1.125rem
}
.main-body .navigation-box {
    height: 50px;
    color: #777;
    border-bottom: 1px solid #e8e8e8;
    -webkit-box-shadow: 0 2px 8px #f0f1f2;
            box-shadow: 0 2px 8px #f0f1f2;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between
}
.main-body .navigation-box a {
    color: #777;
    text-decoration: none;
    cursor: pointer;
}
.main-body .navigation-box .blog-logo {
    font-size: 1.5rem;
    line-height: 3.125rem;
    text-align: center;
    padding-left: 60px;
    position: relative
}
.main-body .navigation-box .blog-logo .logo {
    position: absolute;
    left: 20px;
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    height: 30px;
    width: 30px;
    background-image: url('data:image/png;base64,');
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 90% 90%;
}
@media screen and (max-width: 700px) {
    .main-body .navigation-box .nav-area {
        display: none;
    }
}
@media screen and (min-width: 700px) {
    .main-body .navigation-box .nav-area {
        padding: 0 20px
    }
    .main-body .navigation-box .nav-area .nav-item {
        display: inline-block;
        height: 50px;
        line-height: 3.125rem;
        text-align: center;
        font-size: 1.25rem;
        padding: 0 10px;
    }
    .main-body .navigation-box .nav-area .nav-item:hover {
        background-color: #f5f5f5;
    }
}
.main-body .content-wrapper {
    height: calc(100% - 50px);
    overflow: auto
}
.main-body .content-wrapper .content-box {
    padding: 15px;
    max-width: 677px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-left: auto;
    margin-right: auto;
}
